import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import '../DatePicker/DatePicker.css'
import 'react-datepicker/dist/react-datepicker.css';
import setHours from 'date-fns/setHours'
import setMinutes from 'date-fns/setMinutes'

const CustomDatePicker = (props) => {
  const [startDate, setStartDate] = useState(null);
  const [minH, setMinH] = useState(9);
  const [minM, setMinM] = useState(0);
  const [maxH, setMaxH] = useState(18);
  const [maxM, setMaxM] = useState(0);
  const [excludeTimesMin, SetexcludeTimesMin] = useState(1);
  const [styleStatus, setstyleStatus] = useState(true);

  const OnDateSelect = (date) => {
    if (props.isDepartmentSelect) {
      setstyleStatus(false)
      var curDate = new Date();
      setStartDate(date)
      if (date.getDate() === curDate.getDate() && date.getFullYear() === curDate.getFullYear() && date.getMonth() === curDate.getMonth()) {
        if (new Date().getHours() >= 18) {
          setMinH(18)
          SetexcludeTimesMin(0)
        }
        else {
          setMinH(new Date().getHours())
          setMinM(new Date().getMinutes())
          SetexcludeTimesMin(1)
        }
      }
      else {
        setMinH(9)
        setMinM(0)
        SetexcludeTimesMin(1)
      }
    }
  }
  return (
    <div>
      {
        styleStatus ?
          <style dangerouslySetInnerHTML={{
            __html: `
            .react-datepicker__time-list-item--disabled{
              display: none;
            }
            .react-datepicker__time-list-item{
              color:#ccc;
            }    
            react-datepicker__time-list-item--selected{
              color:#ccc;
            }`}}></style>
        :
          <style dangerouslySetInnerHTML={{
            __html: `
            .react-datepicker__time-list-item--disabled{
              display: none;
            }
        `}}></style>
      }
      <DatePicker
        selected={startDate}
        onChange={date => OnDateSelect(date)}
        minDate={new Date()}
        showTimeSelect
        timeIntervals={15}
        useWeekdaysShort={true}
        timeCaption=""
        dateFormat="MMMM d, yyyy HH:mm aa"
        minTime={setHours(setMinutes(new Date(), minM), minH)}
        maxTime={setHours(setMinutes(new Date(), maxM), maxH)}
        excludeTimes={[setHours(setMinutes(new Date(), excludeTimesMin), 18)]}
        inline
        use12Hours
      />
    </div>
  );
};

export default CustomDatePicker;
